function Scroll() {
    var els=document.querySelectorAll("li");
    for(let i=0; i<els.length; i++){
        els[i].addEventListener("click", function() {
            var sec=document.querySelector(`.${els[i].getAttribute("scroll")}`)
            if (sec) {
                sec.scrollIntoView({
                    block:"start",
                    behavior :"smooth",
                })
            }
        })
    }
}
export default Scroll;